import gsap from 'gsap';
import ScrollTrigger from 'gsap/dist/ScrollTrigger';
import { exist } from '../../utils';
import { OFFSETS } from '../../utils/constants';

const listAnimation = () => {
	const SELECTORS = {
		section: '.js-list-section',
		title: '.js-list-title',
		listWrap: '.js-list-wrap',
		list: '.js-list-block',
		listItem: '.js-list-item',
		card: '.js-list-card',
		shadow: '.js-list-shadow',
	};

	const $section = document.querySelector(SELECTORS.section);

	if (!exist($section)) return;

	gsap.registerPlugin(ScrollTrigger);

	const $wrap = $section.querySelector(SELECTORS.listWrap);
	const $list = $section.querySelectorAll(SELECTORS.list);
	const $card = $section.querySelector(SELECTORS.card);
	const $title = $section.querySelectorAll(SELECTORS.title);
	const $shadow = $section.querySelectorAll(SELECTORS.shadow);

	const $globalWrapper = document.querySelector('.js-global-wrapper');

	$globalWrapper.style.paddingBottom = `${OFFSETS.power + OFFSETS.extracted + OFFSETS.list}px`;

	const collectListData = () => {
		const data = [];
		$list.forEach((list) => {
			const items = list.querySelectorAll(SELECTORS.listItem);
			const obj = {
				list,
				items,
			};
			data.push(obj);
		});

		return data;
	};

	const data = collectListData();

	const startOffset = OFFSETS.power + OFFSETS.extracted;
	const endOffset = OFFSETS.power + OFFSETS.extracted + OFFSETS.list;

	const tl = gsap.timeline({
		paused: true,
		defaults: {
			ease: 'none',
		},
		scrollTrigger: {
			trigger: $section,
			start: () => (window.innerWidth < 1024 ? `top+=${startOffset} top` : `center+=${startOffset} center`),
			end: () => (window.innerWidth < 1024 ? `top+=${endOffset} top` : `center+=${endOffset} center`),
			pin: '.wrapper',
			scrub: true,
		},
	});

	const wrapHeight = $wrap.clientHeight;

	data.forEach((item, index) => {
		const { items, list } = item;
		const itemsCount = items.length;
		const y = wrapHeight - list.clientHeight;
		const stepDuration = 0.2;
		const offset = index === 0 ? '0' : '-=0.2';

		const middleItems = Array.from(items).slice(4, items.length - 4);
		const ratio = middleItems.length / items.length;

		const finalItem = items[items.length - 5];

		const label = `list_start_${index}`;

		tl.to(
			list,
			{
				opacity: 1,
				x: 0,
				duration: 1,
			},
			offset,
		)
			.to($title[index], {
				opacity: 1,
			})
			.addLabel(label)
			.to(
				list,
				{
					y,
					duration: stepDuration * itemsCount,
				},
				label,
			)
			.to(
				middleItems,
				{
					webkitTextFillColor: '#dce161',
					webkitTextStrokeColor: 'transparent',
					stagger: stepDuration / ratio,
					duration: stepDuration / 2,
				},
				label,
			)
			.to(
				middleItems,
				{
					webkitTextFillColor: 'transparent',
					webkitTextStrokeColor: '#fff',
					stagger: (index) => {
						return index === middleItems.length - 1 ? 0 : (index * stepDuration) / ratio;
					},
					duration: stepDuration / 2,
				},
				`${label}+=${stepDuration / ratio}`,
			)
			.to(finalItem, {
				webkitTextFillColor: '#dce161',
				webkitTextStrokeColor: 'transparent',
				scale: 1.15,
				duration: 1,
			})
			.to(
				list,
				{
					opacity: 0,
					x: 50,
					duration: 2,
				},
				// `-=${stepDuration}`,
				'+=1',
			)
			.to(
				$title[index],
				{
					opacity: 0,
				},
				'<',
			);
	});

	tl.to($card, {
		opacity: 1,
	})
		.to(
			$title[$list.length],
			{
				opacity: 1,
			},
			'<',
		)
		.to(
			$shadow,
			{
				opacity: 0,
			},
			'<',
		)
		.to(
			{},
			{
				duration: 1,
			},
		);
};

export default listAnimation;
